import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
export default {
	components: {
		uniSearchBar,
		cuPanel,
		cuCell,
		cuCellGroup
	},
	data() {
		return {
			name: '',
			title: '计量单位',
			datas: null,
			searchDatas: null
		}
	},
	onLoad(options) {
		this.name = options.name
	},
	onShow() {
		if (this.name == 'unit') {
			this.datas = uni.getStorageSync('productCategory').units
		} else {
			this.datas = uni.getStorageSync('productCategory').subUnits
		}
		this.searchDatas = this.datas
	},
	computed: {
		headerHeight() {
			return this.$headerIsSearchHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		filterItem(item) {
			return { 'name': item }
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					return item.name.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		}
	}
}
